.App {
  text-align: center;
  margin-top: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
  margin-top: 0; 
  font-family: "Bitter";
}

.App-footer {
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
  margin-bottom: 0; 
  font-family: "Bitter";
  position: absolute; 
  bottom:0;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
  font-family: 'Bitter';
  src: local('Bitter-Regular'), url(./fonts/Bitter-Regular.otf) format('opentype');
}

@font-face {
  font-family: 'Henrik';
  src: local('Henrik-Regular'), url(./fonts/Henrik-Regular.otf) format('opentype');
}